import React, { useEffect, useState, useRef } from 'react';
import { useDispatch } from 'react-redux';
import Draggable from 'react-draggable';
import YouTube from 'react-youtube';

import './index.css';
import MicMute0 from '../../images/mic-mute.svg'
import MicMute1 from '../../images/mic-mute1.svg'
import closevideo from '../../images/closevideo.svg'
import WebRTC from '../../webrtc';

const YoutubeUpload = (props) => {
    const nodeRef = React.useRef(null);
    const videoRef = useRef(null);
    const [youtubeMute, setYoutubeMute] = useState('off')
    const dispatch = useDispatch();

    const opts = {
        width: 380,
        height: 190,
        // origin: 'https://webrtc.bcisummit.com',
        playerVars: {
            autoplay: 0,
        }
    };

    const youtubeMuteAudio = 'youtubeAudio_'+props.video.value 
    const videoid = 'video_'+props.video.value 

    const defX = - 180
    const defY = - 100
    var currentTime

    useEffect(() => {
        setTimeout(onTimer, 500);
    },[]);
    function onTimer(){
        const me_ele = document.getElementById('screen_me')
        const ele = document.getElementById('video_' + props.video.value)
        if(me_ele && ele && ele.style.transform){
            const screenMePos = {x: parseFloat(me_ele.style.left), y: parseFloat(me_ele.style.top)}
            const posArr = ele.style.transform.match(/[+-]?\d+/g)
            const screenCurrObjPos = {x: parseInt(posArr[0])+382/2, y: parseInt(posArr[1])+214/2}
            const dist = Math.sqrt((screenMePos.x-screenCurrObjPos.x)*(screenMePos.x-screenCurrObjPos.x) + 
                                    (screenMePos.y-screenCurrObjPos.y)*(screenMePos.y-screenCurrObjPos.y))

            var vol = Math.max(0.01, Math.min(1, Math.pow((200 / dist), 4)))
            if (videoRef && videoRef.current) {
                videoRef.current.internalPlayer.setVolume(vol* 100 * (ele.muted==='on' || ele.muted===undefined? 1: 0));
            }
        }

        setTimeout(onTimer, 1000)
    }

    const handleClose = (value) => {
        dispatch({type: 'youtube_remove', value})
        WebRTC.getInstance().youtubeRemove({value: value})
    }
    const handleStop = (e) => {
        dispatch({type: 'backgound_moving', value: {bgMoving: false} })
        dispatch({type: 'youtube_position', value: { id: 'me', transform: nodeRef.current.style.transform} })
        WebRTC.getInstance().youtubePosition({transform: nodeRef.current.style.transform, name: props.video.name})
    }
    const handleStart = (event) => {
        dispatch({type: 'backgound_moving', value: {bgMoving: true} })
    }
    const handleStateChange = (event, param1, param2) => {        
        if (!props.video.timechange && props.video.id === 'me') {
            currentTime = videoRef.current.internalPlayer.getCurrentTime()
            currentTime.then(function(result) {
                WebRTC.getInstance().youtubePosition({curtime: result, name: props.video.name, timechange: true})
            })
        } 
    }
    {
        currentTime = props.video.curtime
        if(videoRef && videoRef.current)
            videoRef.current.internalPlayer.seekTo(currentTime, false)
    }
    const handleYouTubePause = (event) => {
        WebRTC.getInstance().youtubePlay({ name: props.video.name, videoplay: false })
        WebRTC.getInstance().youtubePosition({curtime: currentTime, name: props.video.name, timechange: false})
    }
    const handleYouTubePlay = (event) => {
        WebRTC.getInstance().youtubePlay({ name: props.video.name, videoplay: true })
    }
    const toggleChange = (event)=>{
        const ele = document.getElementById('video_' + props.video.value)

        if (youtubeMute === 'on') {
            if (videoRef.current) {
                document.getElementById(youtubeMuteAudio).src = MicMute0
                setYoutubeMute('off')
                ele.muted = 'on'
            }
        }
        else {
            if (videoRef.current) {
                document.getElementById(youtubeMuteAudio).src = MicMute1
                ele.muted = 'off'
                setYoutubeMute('on')
            }
        }
    }
    if (nodeRef.current && props.video.transform) {
        nodeRef.current.style.transform = props.video.transform;
    }
    if (videoRef.current) {
        if (props.video.videoplay) {
            videoRef.current.internalPlayer.playVideo()
        }
        else {
            videoRef.current.internalPlayer.pauseVideo()
        }
    }
    if (props.userClose) {
        if (props.video.id==='me') {
            var closeVal = props.video.value
            dispatch({type: 'youtube_remove', closeVal })
            WebRTC.getInstance().youtubeRemove({value: closeVal})
        }
        WebRTC.getInstance().closeClient()
        window.location.href = '/room?space='+props.room;
    }
    
    return (
        <Draggable nodeRef={nodeRef} scale={props.cur} defaultPosition={{x: defX, y: defY}} disabled={props.video.id==='me' ? false: true} onStart={(e) => handleStart(e)} onStop={(e) => handleStop(e)} >
            <div data-v-c1f79ed4="" data-v-6e496afa="" data-v-7e3fe256="" ref={nodeRef} key={props.video.id} id={videoid}
                className="youtube-el youtube-frame" style={{ transform: props.video.transform, zIndex: props.video.id==='me'?10:5 }}>
                <div data-v-a995c326="" data-v-c1f79ed4="" className="header pointer hidden" >
                    <div data-v-a995c326="" className="title" >Pinned by {props.video.username}</div>
                    {props.video.id==='me' &&
                        <div onClick={() => handleClose(props.video.value)} >
                            <img data-v-6a2f6b36 className="close" src={closevideo} alt='closevideo.svg' />
                        </div>
                    }
                </div>
                <div data-v-c1f79ed4="" className={`player ${props.video.id==='me'?null:'pointer-events-none'}`} >
                    <YouTube
                        ref={videoRef}
                        videoId={props.video.value}
                        opts={opts}
                        onPlay={props.video.id==='me'?(e)=>handleYouTubePlay(e): null}
                        onPause={props.video.id==='me'?(e)=>handleYouTubePause(e):null}
                        onStateChange={props.video.id==='me'?(e)=>handleStateChange(e):null}
                    />
                </div>
                { props.video.id==='me' ? null :
                    <img data-v-c1f79ed4="" id={youtubeMuteAudio} svg-inline="" src={MicMute0} className="mute" 
                        onClick={(e)=>toggleChange(e)} alt="youtubeAudio"></img>
                }
            </div>
        </Draggable>
    );
}
export default YoutubeUpload;