import React, { useState, useEffect } from "react";
import { useSelector, useDispatch} from 'react-redux';
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import * as qs from 'query-string'

import Navbar from '../components/navbar';
import WebRTC from '../webrtc';
import Screen from '../components/screen';
import YoutubeUpload from '../components/youtubeupload';
import ImageUpload from '../components/imageupload';


import './index.css';

function Conference(props) {
    const dispatch = useDispatch()
    const query = qs.parse(props.location.search);
    const users = useSelector(state=>state.users);
    const bgMoveObj = useSelector(state=>state.screens.bgMoving);
    const videoObj = useSelector(state=>state.screens.videos);
    const imageObj = useSelector(state=>state.screens.images);

    useEffect(()=>{
        console.log('sdasdsad')
        var userName = window.localStorage.getItem('userName')
        WebRTC.getInstance().startConference(dispatch, null, query.space, query.uname);
    },[])

    const [wheelX, setWheelX] = useState(-1000)
    const [wheelY, setWheelY] = useState(-1000)
    const [wheelZ, setWheelZ] = useState(-1000)
    const [curScale, setCurScale] = useState(1)
    const [userClose, setUserClose] = useState(false);

    const handleWheel = (event) => {
        var temp
        if (event.previousScale < event.scale)
            temp = 2
        else
            temp = -2

        if (event.scale === event.options.maxScale || event.scale === event.options.minScale)
            temp = 0

        setCurScale(event.scale)
        setWheelX(wheelX + event.scale * temp)
        setWheelY(wheelY + event.scale * temp)
        setWheelZ(wheelZ + event.scale * temp)
    }
    const handlePanning = (event) => {
        event.options.disabled = bgMoveObj.bgMoving
    }
    
    const handleSetUserClose = (value) => {
        setUserClose(value)
    }
    return (
    <div data-v-12a888fb="" className="space">
        <Navbar onSetUserClose={handleSetUserClose} videoObj={videoObj} imageObj={imageObj} spaceName='tryme' /> 
        <div data-v-6e496afa="" data-v-12a888fb="" className="session" style={{}} >
            <div className="small-map-background" style={{transform: 'translate3d('+ wheelX +'px, '+ wheelX +'px, '+ wheelX +'px)'}}></div>
            <TransformWrapper
                defaultScale={1}
                wheel={{step: 100}}
                pan={{ paddingSize: 0}}
                scalePadding={{ size: 0}}
                options={{disabled: false, centerContent: true, limitToBounds:true, maxScale: 3}}
                onWheel={(e)=>handleWheel(e)}
                doubleClick={{disabled: true}}
                onPanning={(e)=>handlePanning(e)}
            >
                <React.Fragment>
                    <TransformComponent>
                    <div data-v-7e3fe256="" className="back map-background">
                        <div className='videoFrame' id='videoFrame'>
                        {
                            users.map((user) => <Screen key={user.id} cur={curScale} user={user} />)
                        }
                        {
                            videoObj.map((video) => <YoutubeUpload key={video.name} video={video} cur={curScale} userClose={userClose} room={query.space} />)
                        }
                        {
                            imageObj.map((image) => <ImageUpload key={image.value} image={image} cur={curScale} userClose={userClose} room={query.space} />)
                        }
                        </div>
                    </div>
                    </TransformComponent>
                </React.Fragment>
            </TransformWrapper>
        </div>
    </div>
    );
}

export default Conference;
