import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Rnd } from "react-rnd";

import './index.css';
import closeimage from '../../images/closevideo.svg'
import WebRTC from '../../webrtc';

const ImageUpload = (props) => {
    const nodeRef = React.useRef(null);
    const dispatch = useDispatch();

    const handleClose = (value) => {
        dispatch({type: 'image_remove', value})
        WebRTC.getInstance().imageRemove({value: value})
    }
    const handleStart = (event) => {
        dispatch({type: 'backgound_moving', value: {bgMoving: true} })
    }
    const handleStop = (e) => {
        dispatch({type: 'backgound_moving', value: {bgMoving: false} })
        dispatch({type: 'image_position', value: { id: 'me', transform: nodeRef.current.parentNode.style.transform} })
        WebRTC.getInstance().imagePosition({transform: nodeRef.current.parentNode.style.transform, name: props.image.name})
    }
    if (nodeRef.current && props.image.transform) {
        nodeRef.current.parentNode.style.transform = props.image.transform;
    }
    if (props.userClose) {
        if (props.image.id==='me') {
            var closeVal = props.image.value
            dispatch({type: 'image_remove', closeVal })
            WebRTC.getInstance().imageRemove({value: closeVal})
        }
        WebRTC.getInstance().closeClient()
        window.location.href = '/room?space='+props.room;
    }

    const [cWid, setCWid] = useState(275)
    const [cHei, setCHei] = useState(183)
    const [xPos, setXPos] = useState(-135)
    const [yPos, setYPos] = useState(-100)

    return (
        <Rnd
            noderef={nodeRef}
            size={{ width: cWid,  height: cHei }}
            position={{ x: xPos, y: yPos }}
            onDragStart={(e) => handleStart(e)}
            onDragStop={(e, d) => { 
                setXPos(d.x)
                setYPos(d.y)
                handleStop(e)
            }}
            scale={props.cur}   
            style = {{zIndex: props.image.id==='me'?10:5}}
            onResizeStop={(e, direction, refval, delta, position) => {
                setCWid(refval.style.width)
                setCHei(refval.style.height)
            }}
            disableDragging={props.image.id==='me' ? false: true}
        >
            <div ref={nodeRef} key={props.image.id} style={{width:'100%'}} >
                <div className="cus_header pointer hidden">
                    <div className="cus_title">Pinned by {props.image.username}</div>
                    {props.image.id==='me' &&
                        <div onClick={() => handleClose(props.image.value)} className="cus_close" >
                            <img data-v-6a2f6b36 src={closeimage} alt='closevideo.svg' />
                        </div>
                    }
                </div>
                <img src={props.image.value} className="uploadedimage" alt="map-transparent" />
            </div>
        </Rnd>
    );
}
export default ImageUpload;